/* ---- FOOTER ---- */
footer div.row {
  background-color: var(--main-color-maastricht_blue);
  color: var(--main-color-heliotrope);
  text-align: center;
  padding: 1.5%;
  border-radius: 20px;
  font-weight: bold;
  box-shadow: 0px 5px 10px var(--main-color-maastricht_blue);
}
footer ul {
  display: flex;
  justify-content: space-around;
  list-style: none;
  padding-left: 8%;
}
footer ul li a {
  text-decoration: none;
  font-size: 1em;
}
footer ul li a:after {
  content: "";
  display: block;
  width: 0;
  height: 3px;
  background: #fff;
  transition: width 0.3s;
}
footer ul li a:hover {
  color: white !important;
}
footer ul li a:visited {
  color: var(--main-color-heliotrope);
}
footer ul li a:hover:after {
  width: 100%;
  z-index: 100;
}
footer button {
  z-index: 1;
  position: relative;
  font-size: inherit;
  font-family: inherit;
  color: var(--main-color-heliotrope);
  outline: none;
  border: none;
  background-color: var(--main-color-maastricht_blue);
  overflow: hidden;
}
footer button::after {
  content: "";
  z-index: -1;
  background-color: hsla(0, 0%, 100%, 0.2);
  position: absolute;
  top: -50%;
  bottom: -50%;
  width: 1.25em;
  transform: translate3d(-525%, 0, 0) rotate(35deg);
}
footer button:hover::after {
  transition: transform 0.45s ease-in-out;
  transform: translate3d(200%, 0, 0) rotate(35deg);
}
/* ---- END FOOTER ---- */
